*{
  margin: 0;
  padding:  0;
  font-family: sans-serif;
}
.login-box{
text-align: center;
background: #fff;
padding: 20px;
margin: 8% auto;
box-shadow: 0 0 3px 0 rgba(0,0,0,0.3);
width: 300px;
}
.login-box h2{
color: #1c8adb;
margin-bottom: 30px;
}
.login-box input{
    border-radius: 20px;
padding: 10px;
margin: 10px 0;
width:  95%;
border: 1px solid #999;
outline: none;
}
.login-box button{
    color: #fff;
    text-transform: uppercase;
    width: 95%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 20px;
    cursor: pointer;
    outline: none;
    background-color:#1c8adb ;
    font-size: 15px;

}
.login-box1{
