    }
    
    .myList{
        width: 300px;
        padding: 0px;
        margin: 100PX auto;
        display: flex;
        float: left;
        justify-content: center;
        border:  1px solid #bbb;
    }
    .myList li{
        list-style: none;
        background: #eee;
        border-top: 1px solid #ccc;
        
    }
    .container{
        position: relative;
        width:  100%;
        max-width: 100px;
        min-height: 100px;
        background: #fff;
        margin: 50px;
        display: grid;
        grid-template-columns: 1fr 2fr;
        box-shadow:  0 35px 55px rgb(0, 0, 0,0.1);  
    }
    .profileText{
        position: relative;
        display: flex;
        flex-direction: column;
        padding-bottom: 20px;
        align-items: center;
        border-bottom: 1px solid rgb(255,255,255,0.2);
    }
    .profileText{
        position: relative;
        width: 200px;
        height: 200px;
        border-radius: 50%;
        overflow: hidden;
    }
    .profileText .imgBx img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .profileText h2{
        color: white;
        font-size: 1.5em;
        margin-top: 20PX;
        text-transform: uppercase;
        align-items: center;
        font-weight: 600;
        line-height: 1.4em;
    }
    .profileText h2 span{
        font-style: 0.8em;
        font-weight: 300;
    }
    .container .left_side{
        position: relative;
        background: #003147;
        padding: 40px;
    }
    
    .container .rigth_side{
        position: relative;
        background: #fff;
        padding: 40px;
    }
    
    .wrappers{
